import * as React from 'react';
import { Link } from 'gatsby';
import * as styles from './RM.module.scss';

const ReadMore = ({ children, to }) => {
  return (
    <>
      <Link to={to} className={'uk-link-muted uk-text-uppercase ' + styles.readmore}>
        <span>{children}</span>
        <span className={styles.icon + ' uk-link-reset'} data-uk-icon={`icon: chevron-right ; ratio: 0.75`} />
      </Link>
    </>
  );
};

export default ReadMore;
