import * as React from 'react';
import { Link } from 'gatsby';
//import Img from 'gatsby-image';
import { GatsbyImage as Img } from 'gatsby-plugin-image';
import { formatDate } from '~src/_utils/fortmatDate';

export const ArticleCard = ({ article }) => {
  return (
    <Link to={article.uri}>
      <article className="uk-card uk-card-default uk-card-small uk-article uk-overflow-hidden uk-box-shadow-hover-large uk-height-1-1 tm-ignore-container">
        {article.featuredImage && (
          <div className="tm-ratio tm-ratio-16-9">
            <figure className="tm-media-box uk-cover-container uk-margin-remove">
              <Img
                image={article.featuredImage.node.localFile.childImageSharp.gatsbyImageData}
                alt={article.title}
                data-uk-cover
              />
            </figure>
          </div>
        )}
        <div className="uk-card-body">
          <div className="uk-article-body">
            {/* Date */}
            <div className="uk-article-meta uk-margin-xsmall-bottom">
              <time> {formatDate(article.date)}</time>
            </div>
            {/* Title */}
            <div>
              <h3 className="uk-margin-remove">{article.title}</h3>
            </div>
            {/* Desc */}
            {article.excerpt && (
              <div dangerouslySetInnerHTML={{ __html: article.excerpt }} className="uk-margin-small-top"></div>
            )}
          </div>
        </div>
      </article>
    </Link>
  );
};
