import * as React from 'react';
import { ArticleCard } from '../Blog/Article';
import ReadMore from '../ReadMore/ReadMore';

export const Blog: React.FC<{ data }> = ({ data }) => {
  const blogPosts = data.blog.nodes;
  return (
    <section className="uk-section uk-section-small">
      <div className="uk-container">
        <h2 className="uk-text-center">آخرین مقالات</h2>
        <div className="uk-grid-medium uk-grid-match-uk-child-width-1-1 uk-child-width-1-2@s" data-uk-grid>
          {blogPosts.map(article => (
            <div key={article.title}>
              <ArticleCard article={article} />
            </div>
          ))}
        </div>

        <div className="uk-margin uk-text-center">
          <ReadMore to="/blog">همه مقالات</ReadMore>
        </div>
      </div>
    </section>
  );
};
