import * as React from 'react';
import { graphql } from 'gatsby';
import { Layout } from '../context/Layout';
import { HeroCarousel } from '~src/components/Index/HeroCarousel';
import { Advantages } from '~src/components/Index/Advantages';
import { Subscribe } from '~src/components/Index/Subscribe';
import { InformationBlock } from '~src/components/Index/InformationBlock';
import { Blog } from '~src/components/Index/Blog';
import { Trending } from '~src/components/Index/Trending';
import { Brands } from '~src/components/Index/Brands';
//import thumbnail from '~src/_assets/images/ahanbama-thumbnail.jpg';

const IndexPage = ({ data }) => {
  const seo = {
    home: true,
    //image: thumbnail,
  };
  return (
    <Layout seo={seo}>
      <HeroCarousel />
      <Trending data={data} />
      <Blog data={data} />
      <Brands />
      <InformationBlock data={data} />
      <Subscribe />
      <Advantages />
    </Layout>
  );
};

export default IndexPage;
export const query = graphql`
  query IndexPageQuery {
    products: allWpProduct(filter: { featured: { eq: true } }, limit: 8) {
      nodes {
        id
        name
        productCategories {
          nodes {
            name
          }
        }
        attributes {
          nodes {
            name
            variation
            options
          }
        }
        image {
          localFile {
            childImageSharp {
              gatsbyImageData(layout: FIXED)
            }
          }
        }
        ... on WpSimpleProduct {
          id
          name
          price
          uri
        }
        ... on WpVariableProduct {
          id
          attributes {
            nodes {
              name
              variation
              options
            }
          }
          name
          price
          uri
        }
      }
    }
    news: allWpPost(
      limit: 3
      sort: { fields: date, order: DESC }
      filter: { categories: { nodes: { elemMatch: { id: { eq: "dGVybToyNzc=" } } } } }
    ) {
      nodes {
        id
        title
        date
        excerpt
        uri
      }
    }
    blog: allWpPost(limit: 2, filter: { categories: { nodes: { elemMatch: { id: { eq: "dGVybToyODE=" } } } } }) {
      nodes {
        id
        title
        date
        excerpt
        uri
        featuredImage {
          node {
            localFile {
              childImageSharp {
                gatsbyImageData(layout: FULL_WIDTH)
              }
            }
          }
        }
      }
    }
  }
`;
